"use scrict";
import { logo } from "@/config/image.js";
import Progress from "@/components/Progress/index.vue";
import { setStorage, getStorage, removeStorage } from "@/utils/storage.js";

export default {
	data() {
		return {
			logo,
			degree: 0,
			limit: getStorage("welcomeLimit") || 40
		}
	},
	onLoad(options) {
		this.$eventBus.$on("setWelcomeDegree", value => {
			if (this.degree < value) {
				this.degree = value;
				this.limit = 2 * value;
				value < 79 ? setStorage("welcomeLimit", 2 * value) : removeStorage("welcomeLimit");
			}
		});
		this.growingForDegree();
	},
	methods: {
		growingForDegree: function () {
			setInterval(() => {
				this.degree < this.limit && this.degree < 95 && this.degree++;
			}, 20);
		}
	},
	components: {
		Progress
	}
}
